import React from 'react'
import './header.css'
import headerPNG from '../../assets/header.jpg'
function Header() {
	return (
		<div>
			<img className='header-img' src={headerPNG}></img>
			<div className='header-text'>
				<h1>Hola, soy Israel</h1>
				<p>Un programador estudiante de la UTEM</p>
			</div>
		</div>
	)
}

export default Header
